import React, { useState, useEffect, useCallback } from 'react'
// import { makeStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Box from '@material-ui/core/Box'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'

const ReadTale = props => {
    const [tale, setTale] = useState(null)
    const [sb, setSB] = useState([])    // sb = Storyboard, sbI[], sbI = { stID, selectedCID }
    const { match } = props
    const goTo = url => props.history.push(url)
    
    useEffect(() => {
        let storyID = match.params.id
        // If it is a local story
        // Then the ID should start with "local"
        if (storyID.indexOf("local") >= 0) {
            storyID = storyID.trim().replace("local-", "")
            const allReactales = JSON.parse(window.localStorage.getItem("allReactales")) || {}
            const theTale = allReactales[storyID]
            console.log(theTale)
            setTale(theTale)
            // setSB([{ stID: theTale.id }])
            window.rto.createTimeline('timelineContainer', theTale)
        }
    },
    [])

    return (
        <Box p={2} mb={5}>
            {   
                tale &&
                <Typography variant="h4" component="h1">
                    {tale.info.name}
                </Typography>
            }

            <div id="timelineContainer"></div>
        
        </Box>
    )
}

export default ReadTale